.slider-video {
	height: 415px;
	margin-top: 20px;
	position: relative;

	&__item {
		display: flex;
		height: 100%;
		width: 100% !important;
		max-width: 870px !important;
		margin-right: 30px; }

	&__column-l {
		max-width: 570px;
		max-height: 415px;
		width: 100%;
		height: 100%;
		margin-right: 30px; }

	&__column-s {
		max-width: 270px;
		max-height: 415px;
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-between; }

	&__big {
		height: 100%; }

	&__small {
		max-height: 190px;
		height: 100%; }

	&__play {
		position: absolute;
		content: '';
		z-index: 2;
		transform: translate(-50%, -50%); } }
