.play-area {
	position: absolute;
	width: 77px;
	height: 77px;
	border-radius: 50%;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	display: flex;
	align-items: center;
	justify-content: center;
	z-index: 3;
	cursor: pointer;



	&__btn {
		width: 77px;
		height: 77px;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: $accent-blue;
		border-radius: 50%;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		z-index: -1; }

	&__pulse {
		position: absolute;
		content: '';
		width: 125px;
		height: 125px;
		background-color: #ffffff;
		z-index: -2;
		border-radius: 50%;
		opacity: .1;
		animation: pulse 3s ease-out infinite; }


	svg {
		width: 22px;
		height: 22px;
		fill: #000000; } }



@keyframes pulse {
	0% {
		transform: scale(.6);
		opacity: 0; }
	50% {
		transform: scale(1);
		opacity: .1; }
	100% {
		transform: scale(.6);
		opacity: 0; } }
