.top {
	position: relative;

	&__social {
		position: absolute;
		content: '';
		left: 48px;
		top: 180px;
		display: flex;
		flex-direction: column; }


	&__social-link {
		text-decoration: none;


		&:not(:last-child) {
			margin-bottom: 62px; }

		&:hover {

			svg {

				fill: $accent-blue; } }

		svg {
			transition: .6s ease-out;
			width: 24px;
			height: 24px;
			fill: #ffffff; } }

	&__down {
		position: absolute;
		content: '';
		bottom: 67px;
		left: 45px;
		transform: rotate(90deg);
		animation: up-down 2s ease-out infinite;

		svg {
			width: 21px;
			height: 15px;
			fill: #ffffff; }

		@media screen and (max-width: 1350px) {
			bottom: 120px; } }

	&_biograpgy {
		background: url('../img/Первый экран.jpg') $bg-dark-gray no-repet center;
		background-size: cover; } }


@keyframes up-down {
	0% {
		transform: translateY(0) rotate(90deg); }
	50% {
		transform: translateY(-20px) rotate(90deg); }
	100% {
		transform: translateY(0) rotate(90deg); } }
