.music-card {
	width: 100%;


	&__img {
		width: 100%;
		max-height: 250px;
		background-color: $bg-gray;
		object-fit: cover;

		img {
			width: 100%;
			height: auto; } }

	&__content {
		padding: 20px 40px 26px;
		background-color: $bg-gray;
		border: 1px solid $border-color;
		border-top: none;

		@include xxs-block() {
			padding: 20px 20px 26px; } }

	&__audio {
		padding: 24px 0 44px; } }
