.b-video {
	padding-top: 100px;
	padding-bottom: 100px;

	&__container {
		@include wrapper(); }

	&__inner {
		@include row-flex(); }

	&__video {
		margin-bottom: 30px; }

	&__col_3 {
		@include col();
		@include size(3); }

	&__col_6 {
		@include col();
		@include size(6); }

	&__col_6 {
		@include col();
		@include size(6); } }
