.projects {
	display: flex;
	flex-wrap: wrap;
	margin-right: -15px;
	margin-top: -15px;
	justify-content: space-between;

	@include xs-block() {
		margin-right: 0; }


	&__item {
		transition: .6s ease-out;

		&_border {
			border: 1px solid $border-color; }

		&:hover {
			opacity: .8; }

		&__col_6 {
			max-width: 400px;
			width: 100%;
			height: 170px;

			margin-bottom: 35px;
			margin-top: 35px;
			box-sizing: border-box; } }


	&__img {
		width: 100%;
		max-width: 100%;
		height: auto;
		max-height: 100%;
		box-sizing: border-box; } }






