.sub-header {
	width: 100%;
	height: 200px;
	background-color: $bg-dark-gray;
	background-size: cover;
	padding: 35px 0 30px 0;
	box-sizing: border-box;

	&__container {
		@include wrapper(); }

	&__inner {
		display: flex; }

	&__descr {
		display: flex;
		flex-direction: column; }

	&__h1 {
		text-transform: uppercase; }

	&_blog {
		background: url('../img/blog-bg.jpg') no-repeat center; }

	&_music {
		background: url('../img/music-bg.jpg') no-repeat center; }

	&_appearances {
		background: url('../img/appearances-bg.jpg') no-repeat center; } }





