.hamburger {
	position: relative;
	display: flex;
	height: 100%;

	cursor: pointer;

	&:hover {

		.hamburger__item {
			background-color: $accent-blue;
			transform: translateX(-6px); }

		&::after {
			background-color: $accent-blue;
			transform: translateX(-6px); }

		&::before {
			background-color: $accent-blue;
			transform: translate(3px, -50%); } }



	&__item {
		transition: .6s ease-out;
		width: 100%;
		height: 2px;
		background-color: #fff; }


	&::before {
		transition: .6s ease-out;
		position: absolute;
		content: '';
		width: 100%;
		height: 2px;
		background-color: #fff;
		top: 50%;
		left: 0;
		transform: translateY(-50%); }


	&::after {
		transition: .6s ease-out;
		position: absolute;
		content: '';
		width: 100%;
		height: 2px;
		background-color: #fff;
		bottom: 0; } }





