.bar {
	height: 5px;
	width: 100%;
	background-color: $border-color;
	position: relative;


	&__progress {
		position: absolute;
		content: '';
		width: 60%;
		height: 100%;
		background-color: $accent-blue; }


	&__load {
		position: absolute;
		width: 90%;
		height: 100%;
		content: '';
		background-color: $link-color; }

	&__handler {
		content: '';
		width: 15px;
		height: 15px;
		background-color: $accent-blue;
		position: absolute;
		right: 0;
		border-radius: 50%;
		top: -5px;
		cursor: pointer;

		z-index: 100; } }
