.wide-slider {
	padding: 115px 0;
	display: flex;
	flex-wrap: wrap;

	&__descr {
		justify-content: flex-end; }

	&__content {}

	&__container {
		overflow-x: hidden;
		width: 100%;
		overflow-y: hidden; }

	&__info {
		max-width: 270px;
		width: 100%;
		margin-right: 30px;

		@include md-block() {
			margin-right: auto;
			padding-left: 15px; } }


	&__col_3 {
		display: flex;
		width: calc(50% - 285px); }

	&__col_9 {
		display: flex;
		width: calc(50% + 285px); }

	&__col_md_12 {
		@include md-block() {
			width: 100%; } } }


