.header {
	width: 100%;
	min-height: 80px;
	padding-left: 40px;
	padding-right: 50px;
	padding-top: 17px;
	padding-bottom: 17px;
	color: #ffffff;
	display: flex;
	align-items: center;
	box-sizing: border-box;

	background-color: $bg-dark-gray;

	&__logo {
		display: flex;
		align-items: center;

		&_large {
			width: 46px;

			img {
				width: 100%;
				min-width: 100%;
				height: auto; } } }

	&__menu {
		width: 25px;
		height: 20px;
		margin-left: auto; }

	&__nav {
		margin-left: 19.2%;

		@include sm-block() {
			display: none; } }

	&_transparent {
		background-color: transparent; }

	&_large {
		padding-top: 38px; }

	&_fixed {
		position: fixed;
		z-index: 30; } }
